import { Component, OnInit } from '@angular/core';
declare var jquery:any;
declare var $:any;

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit {

  constructor() { }

  ngOnInit() {
    $('#welcomeStatement > p').delay(100).animate({'opacity':'1'},4000);
    $('#welcomeStatement > img').delay(100).animate({'opacity':'1'},2500);
    $('#aboutMe').delay(100).animate({'opacity':'1'},2000);
    $(window).scroll( function()
    {
        $('.page-box').each( function(i)
        {
            var bottom_of_object = $(this).position().top + $(this).outerHeight();
            var bottom_of_window = $(window).scrollTop() + $(window).height();

            if( bottom_of_window > bottom_of_object )
            {
                $("#pagesMenu").animate({'opacity':'1'},500);
                $('.page-box:nth-child(1)').animate({'opacity':'1'},1500);
                $('.page-box:nth-child(2)').delay(700).animate({'opacity':'1'},1500);
                $('.page-box:nth-child(3)').delay(1000).animate({'opacity':'1'},1500);
                $('.page-box:nth-child(4)').delay(1700).animate({'opacity':'1'},1500);
                $("#pagesMenu").delay(2000).queue(function()
                {
                  $(this).addClass("pagesBackgroundChange");
                  $(this).dequeue();
                });
                // $("#pagesMenu").fadeIn("fast", function() {
                //   $(this).addClass("pagesBackgroundChange");
                // });
            }

        });

    });
  }

}
